<template>
  <div class="hello">
    <button v-on:click="donwload">Download Latest Version</button>
  </div>
</template>

<script>
export default {
  name: "DownloadLatestVersion",
  props: {
    url: String
  },
  methods: {
    donwload: function() {
      this.url = "https://github.com/NO1225/NO1225.github.io/releases/download/v1.0.13/Epub-Reader-TTS.Setup.msi";
      var fileLink = document.createElement("a");
      fileLink.href = this.url;
      fileLink.setAttribute("download", "Epub-Reader-TTS.Setup.msi");
      document.body.appendChild(fileLink);

      fileLink.click();
    }
  }
};
</script>

